@import url(https://fonts.googleapis.com/css?family=Darker+Grotesque&display=swap);

/* Fonts */
/* Global */
body {
  margin: 0;
  padding-top: 63px;
  /* Actual height of nav is 63px */
  font-family: 'Darker Grotesque', sans-serif;
  font-size: 1.3em;
  overflow-x: hidden; }

section {
  padding-bottom: 5%; }

h1, h2, h3, h4, h5 {
  text-transform: uppercase;
  color: black;
  font-weight: 500; }

b {
  color: black; }

@media (min-width: 1400px) {
  .container {
    max-width: 1400px; } }

/* Common Elements */
.btn-primary {
  background-color: black;
  border: 0;
  border-radius: 0;
  color: black; }

.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open > .dropdown-toggle.btn-primary {
  color: white;
  background-color: black;
  font-weight: bold;
  text-decoration: none !important; }

.no-underline:hover {
  text-decoration: none; }

a {
  color: #80848a; }

hr {
  color: black; }

/* Navbar */
.navbar {
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-transition: background-color 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: background-color 0.3s ease-in-out; }
  .navbar .nav-link {
    text-transform: uppercase; }
  .navbar a {
    color: white; }
  .navbar .navbar-brand {
    color: white; }

.navbar-light .navbar-nav .nav-link {
  color: white; }
  .navbar-light .navbar-nav .nav-link :hover {
    border-bottom: solid white 1px; }

.navbar-light .navbar-nav .active > .nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show > .nav-link {
  color: white;
  border-bottom: solid white 1px; }

.navbar-masthead {
  background-color: rgba(0, 0, 0, 0);
  -webkit-transition: background-color 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: background-color 0.3s ease-in-out; }

.masthead {
  position: relative;
  width: 100%;
  height: auto;
  min-height: 35rem;
  padding: 15rem 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(22, 22, 22, 0.3)), color-stop(75%, rgba(22, 22, 22, 0.7)), to(#161616)), url(/static/media/airplane-wing.50b8f236.jpg);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(22, 22, 22, 0.3)), color-stop(75%, rgba(22, 22, 22, 0.7)), to(#161616)), url(/static/media/airplane-wing-bw-tint.77114f54.jpg);
  background: -webkit-linear-gradient(top, rgba(22, 22, 22, 0.3) 0%, rgba(22, 22, 22, 0.7) 75%, #161616 100%), url(/static/media/airplane-wing-bw-tint.77114f54.jpg);
  background: linear-gradient(to bottom, rgba(22, 22, 22, 0.3) 0%, rgba(22, 22, 22, 0.7) 75%, #161616 100%), url(/static/media/airplane-wing-bw-tint.77114f54.jpg);
  background-image: url(/static/media/airplane-wing-bw.fe6a5e5e.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
  margin-bottom: 5%;
  margin-top: -63px;
  z-index: -1; }

.masthead h1 {
  font-size: 2.5rem;
  line-height: 2.5rem;
  letter-spacing: 0.8rem;
  color: white; }

.masthead h2 {
  max-width: 20rem;
  font-size: 1rem; }

@media (min-width: 768px) {
  .masthead h1 {
    font-size: 2rem;
    line-height: 2rem; } }

@media (min-width: 992px) {
  .masthead {
    height: 100vh;
    padding: 0; }
  .masthead h1 {
    font-size: 4rem;
    line-height: 4rem;
    letter-spacing: 0.8rem; }
  .masthead h2 {
    max-width: 30rem;
    font-size: 1.25rem; } }

/* Used for media atm */
.masthead-correction {
  margin-top: -63px; }

/* Background media */
.media-container .media {
  top: 0;
  left: 0;
  z-index: -3;
  position: absolute;
  width: 100%;
  height: 100%; }

.media-container .media-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-content: space-between;
  text-align: center;
  min-height: 70vh;
  padding: 2%; }
  .media-container .media-overlay a {
    color: white; }
  .media-container .media-overlay .header-wrapper {
    display: inline-block; }
    @media (max-width: 800px) {
      .media-container .media-overlay .header-wrapper h1 {
        font-size: 90%; } }
  @media (max-width: MEDIA_FONT_SIZE_BP) {
    .media-container .media-overlay .body-wrapper h2 {
      font-size: 100%; }
    .media-container .media-overlay .body-wrapper p {
      font-size: 60%; } }

.media-container h1, .media-container h2, .media-container p {
  color: white; }

.media-container hr {
  border: solid white 1px; }

/* Eye Catcher */
.eye-catcher-container {
  text-align: center; }
  .eye-catcher-container h1, .eye-catcher-container h2, .eye-catcher-container h3 {
    color: black; }

/* presidio-aelf */
#presidio-aelf {
  text-align: center; }
  #presidio-aelf .card-title {
    text-transform: uppercase;
    font-size: 120%;
    color: black;
    border-bottom: solid black 1px;
    padding-bottom: 4%; }
  #presidio-aelf .card {
    margin-bottom: 5vh;
    border: 0;
    max-width: 600px;
    margin: 0 auto 5vh auto; }

/* Statistics */
#statistics {
  text-align: center;
  padding: 5% 0; }
  #statistics .counting-stat {
    font-size: 300%; }

/* World Map */
.map-wrapper {
  min-height: 31vh; }

.jvectormap-container {
  background-color: !important; }

/* Mission Statement */
#mission-statement {
  text-align: center;
  margin-bottom: 5%; }
  #mission-statement .icon {
    color: #80848a; }
  #mission-statement .statement-text {
    font-size: 150%; }
  #mission-statement .emphasis {
    font-weight: bold; }
  #mission-statement .mechanism {
    text-transform: uppercase; }

/* Servies */
#services {
  color: white; }
  #services button {
    background-color: rgba(0, 0, 0, 0.2);
    color: white; }
  #services .btn-primary:hover, #services .btn-primary:focus, #services .btn-primary:active, #services .btn-primary.active, #services .open > .dropdown-toggle.btn-primary {
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
    font-weight: bold;
    text-decoration: none !important; }
  #services h1, #services h2, #services h3, #services p {
    text-align: center;
    color: white; }
  #services .image-bg {
    background-image: url(/static/media/plane-sun-darkened.ec44711b.jpg);
    background-position: top;
    background-attachment: fixed;
    background-repeat: no-repeat;
    padding-top: 2%; }
  #services .service {
    padding: 5% 0%; }
  @media (min-width: 1000px) {
    #services .service {
      padding: 5% 5%; } }

/* Our Team */
#our-team .thumbnail {
  padding-bottom: 10%;
  position: relative; }

#our-team img {
  border: solid 1px rgba(0, 0, 0, 0); }

#our-team img:hover {
  border: solid rgba(0, 0, 0, 0.7) 1px;
  box-shadow: 0px 10px 25px 10px rgba(0, 0, 0, 0.45);
  -webkit-transition: border 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: border 0.3s ease-in-out;
  -webkit-transition: box-shadow 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: box-shadow 0.3s ease-in-out;
  cursor: pointer; }

#our-team .caption {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  bottom: 20%;
  left: 0;
  width: 100%;
  color: white;
  background-color: rgba(0, 0, 0, 0.2); }

#our-team .caption p {
  padding: 0;
  margin: 0; }

#our-team .caption .name {
  font-size: 110%; }

#our-team .caption .title {
  font-size: 80%; }

.team-bio-modal .modal-content {
  border-radius: 0; }

/* Portfolio slides */
.carousel-caption {
  background-color: rgba(0, 0, 0, 0.5); }
  .carousel-caption h3 {
    font-size: 2em;
    color: white; }
  .carousel-caption p {
    color: white; }

/* Recent Transactions */
#recent-transactions {
  padding: 5% 0; }
  #recent-transactions .card:hover {
    box-shadow: 0px 0px 5px 0px black;
    -webkit-transition: border 0.25s ease-in-out;
    /* Safari prior 6.1 */
    transition: border 0.25s ease-in-out;
    -webkit-transition: box-shadow 0.25s ease-in-out;
    /* Safari prior 6.1 */
    transition: box-shadow 0.25s ease-in-out;
    cursor: pointer; }
  #recent-transactions .card-header {
    text-transform: uppercase;
    background-color: black;
    color: white;
    font-weight: 500; }
  #recent-transactions .card-footer {
    background-color: white;
    color: black; }
  #recent-transactions a:hover {
    text-decoration: none; }

